@import "https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  color: #4d4751;
  background-color: #efeef1;
  font-family: Poppins, arial, helvetica, sans-serif;
  font-size: 14px;
  line-height: 1.6;
}

li {
  list-style: none;
}

.breed-select {
  height: 30px;
  width: 200px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid gray;
  border-radius: 5px;
  margin-bottom: 50px;
}

.cat-info {
  text-align: left;
  min-height: 300px;
  max-width: 70%;
  background-color: #fff;
  border-radius: 10px;
  gap: 20px;
  margin: 0 auto;
  padding: 40px 30px;
  display: flex;
}

.cat-info > img {
  width: 300px;
  height: 300px;
  object-fit: cover;
  border-radius: 20px;
  display: block;
}

.temperament {
  font-weight: 700;
}

.temperament > span {
  font-weight: 400;
}

.loader {
  width: 100vw;
  height: 100vh;
  background-color: #f7f9fb;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.loader-hidden {
  opacity: 0;
  visibility: hidden;
}

.loader:after {
  content: "";
  width: 75px;
  height: 75px;
  border: 15px solid #ddd;
  border-top-color: #7449f5;
  border-radius: 50%;
  animation: loading .75s infinite;
}

@keyframes loading {
  from {
    transform: rotate(0turn);
  }

  to {
    transform: rotate(1turn);
  }
}

